<template>
  <div class="min-h-full">
    <notifications
      position="top center"
      classes="notification-custom"
    />
    <router-view class="min-h-full" />
    <div class="text-[12px] mt-[-20px]">
      Copyright @{{ currentYear }} by <a
        href="https://www.facebook.com/toiyeunghegiao"
        target="_blank"
      >Mai Phương</a>, <a
        href="https://www.facebook.com/tranthaihoangit/"
        target="_blank"
      >Thái Hoàng</a>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import constant from '@/utils/constant';
export default {
  name: 'App',
  computed: {
    currentYear() {
      return new Date().getFullYear();
    }
  },
  created() {
    this.checkLogin();
  },
  mounted() {
    // add js responsivevoice
    if (window.rvAgentPlayer === undefined) {
      let responseVoice = document.createElement('script');
      responseVoice.setAttribute('src', 'https://code.responsivevoice.org/responsivevoice.js?key='+constant.RESPONSIVEVOICE_KEY)
      document.head.appendChild(responseVoice);
    }
  },
  methods: {
    ...mapActions('authStore', [
      'checkLogin'
    ]),
  }
}
</script>
