                    <span class="md:ml-4 ml-2 md:text-[16px] text-[12px]">Thêm câu trả lời</span>
                  </button>
                </div>
                <!-- add challenge -->
                <div
                  class="p-[16px] border border-dashed border-color-5 rounded-lg"
                >
                  <!-- add challenge -->
                  <button
                    type="button"
