            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="3"
                stroke="currentColor"
                class="md:w-6 md:h-6 w-4 h-4"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M12 4.5v15m7.5-7.5h-15"
                />
              </svg>

              <span class="md:ml-4 ml-2 md:text-[16px] text-[12px]">Thêm vòng chơi</span>
            </button>
          </div>
          <!-- control button -->
          <div class="flex justify-center mt-6">
            <button
              class="btn-default btn-default--slate mr-4 flex-shrink-0 md:text-[16px] text-[12px]"
              type="button"
              @click="
                $router.push({
                  name: 'game',
                })
              "
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="3"
                stroke="#ffffff"
                class="md:w-6 md:h-6 w-4 h-4"
              ><path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M15.75 9V5.25A2.25 2.25 0 0013.5 3h-6a2.25 2.25 0 00-2.25 2.25v13.5A2.25 2.25 0 007.5 21h6a2.25 2.25 0 002.25-2.25V15M12 9l-3 3m0 0l3 3m-3-3h12.75"
              /></svg>
              <span class="ml-2 md:text-[16px] text-[12px] text-white">Trở về</span>
