</template>
<script>
import { Form, Field, ErrorMessage } from 'vee-validate';
import { localize } from '@vee-validate/i18n';
import constant from '@/utils/constant';
export default {
  components: {
    Form,
    Field,
    ErrorMessage,
  },
  data() {
    return {
      gameData: {
        name: null,
        rounds: [
          {
            title: null,
            timer: null,
            challenges: [
              {
                question: null,
                answers: [
                  {
                    value: null,
                    quantity: null,
                  },
                ],
              },
            ],
          },
        ],
      },
      gameId: null,
    };
  },
  computed: {
    randomBackground() {
      return Math.floor(Math.random() * (1 - 0 + 1)) + 0;
    },
  },
  mounted() {
    this.checkEdit();
    // custom error message
    localize('vi', {
      fields: {
        game_name: {
          required: 'Vui lòng nhập tên trò chơi',
        },
      },
    });
  },
  methods: {
    /**
     * Check edit
     */
    checkEdit() {
      const routeName = this.$route.name;
      if (routeName == 'game_edit') {
        this.gameId = this.$route?.params?.id;
        if (this.gameId) {
          try {
            const gameData =
              localStorage.getItem(
                constant.LOCAL_STORAGE_KEY.GAME_ID_PREFIX + this.gameId
              ) || null;
            // Check data existence
            if (!gameData) {
              this.$notify({
                text: 'Dữ liệu game không tồn tại!', // Notify if game data does not exist
                type: 'error',
              });
              return this.$router.push({ name: 'game' });
            }
            this.gameData = JSON.parse(gameData);
            return true;
          } catch (e) {
            this.$notify({
              text: 'Dữ liệu game không tồn tại!', // Notify if an error occurs while getting game data
              type: 'error',
            });
            return this.$router.push({ name: 'game' });
          }
        } else {
          this.$notify({
            text: 'Dữ liệu game không tồn tại!', // Notify if an error occurs while getting game data
            type: 'error',
          });
          return this.$router.push({ name: 'game' });
        }
      }
    },
    /**
     * Submit form
     */
    onSubmit() {
      this.createGame();
    },
    /**
     * Check valid submit form
     */
    onInvalidSubmit({ values, errors, results }) {
      if (errors) {
        this.$notify({
          text: 'Có dữ liệu không hợp lệ, vui lòng kiểm tra lại biểu mẫu',
          type: 'error',
        });
      }
    },
    /**
     * Create game data to local storage
     */
    createGame() {
      const isValid = this.isDataValid(this.gameData);
      if (isValid.length > 0) {
        isValid.forEach((message) => {
          this.$notify({
            text: message,
            type: 'error',
          });
        })
        return false;
      }

      // update or create
      const gameId =
        constant.LOCAL_STORAGE_KEY.GAME_ID_PREFIX +
        (this.gameId ? this.gameId : this.generateId());
      localStorage.setItem(gameId, JSON.stringify(this.gameData));
      this.$router.push({ name: 'game' });
    },
    /**
     * validate data question
     */
    isDataValid(data) {
      const errors = [];
      let isQuantityValid = true;

      for (const round of data.rounds) {
        let hasQuestion = false; // Reset hasQuestion for each round
        let roundQuantity = 0; // Initialize the total quantity for the round

        for (const challenge of round.challenges) {
          if (challenge.question !== null && challenge.question.trim() !== '') {
            hasQuestion = true;
          }

          const challengeQuantity = challenge.answers.reduce((sum, answer) => sum + answer.quantity, 0);
          roundQuantity += challengeQuantity; // Accumulate the challenge quantity for the round

          if (challengeQuantity > 30) {
            isQuantityValid = false;
          }
        }

        if (roundQuantity > 30) {
          isQuantityValid = false;
        }

        if (!hasQuestion) {
          errors.push(`Vòng "${round.title}" vui lòng ít nhất có một câu hỏi.`);
        }

        if (!isQuantityValid) {
          errors.push(`Vòng "${round.title}" tổng số lượng câu trả lời không được quá 30.`);
        }
      }

      return errors;
    },
    /**
     * Add a answer
     */
    addAnswer(answers) {
      answers.push({ value: null, quantity: null });
    },
    /**
     * Add challenge
     */
    addChallenge(challenges) {
      challenges.push({
        question: null,
        answers: [
          {
            value: null,
            quantity: null,
          },
        ],
      });
    },
    /**
     * add a round
     */
    addRound(rounds) {
      rounds.push({
        title: null,
        timer: null,
        challenges: [
          {
            question: null,
            answers: [
              {
                value: null,
                quantity: null,
              },
            ],
          },
        ],
      });
    },
    /**
     * Remove item array
     */
    removeItemArray(array, index) {
      if (array.length > 1) {
        array.splice(index, 1);
      }
    },
    /**
     * check show remove button
     */
    shouldShowRemoveButton(array) {
      return array.length > 1;
    },
    /**
     * convert array to string
     */
     convertQuestionToString(array) {
      return array.map(element => element.question).join('___');;
    },
    /**
     * convert array to string
     */
     convertAnswerToString(array) {
      return array.map(element => element.value).join('___');;
    },
    /**
     * Total quantity a round
     */
    totalQuantityRound(challenges) {
      let total = 0;
      challenges.forEach((challenge) => {
        challenge.answers.forEach((answer) => {
          total += answer.quantity;
        })
      })
      return total;
    },
    /**
     * random id game
     */
    generateId() {
      const characters = 'abcdefghijklmnopqrstuvwxyz0123456789';
      let id = '';

      for (let i = 0; i < 8; i++) {
        const randomIndex = Math.floor(Math.random() * characters.length);
        id += characters.charAt(randomIndex);
      }
      return id;
    },
  },
};
</script>
